import React, {useState} from 'react'

export default function Formulario({datosConsulta}) {
    const [busqueda, setbusqueda] = useState({
    ciudad:'',
    pais:''

    })


const handleChange = event => {

setbusqueda({
    ...busqueda,
    [event.target.name] : event.target.value
})
console.log(busqueda)

}


const consultarClima = event => {
    event.preventDefault ();


    //pasar hacia el componente principal la busqueda del usuario
    datosConsulta (busqueda);


}

    return (
        <form onSubmit={consultarClima}>
            <div className="input-field col s12">
            <input
            type="text"
            name="ciudad"
            id="ciudad"
            onChange={handleChange}
            />
            <label htmlFor="ciudad">Ciudad</label>

            <div className="input-field col s12">
                <select onChange={handleChange} name="pais">
                    <option value="">Selecciona un Pais</option>
                    <option value="US">Estados Unidos</option>
                    <option value="MX">Mexico</option>
                    <option value="AR">Argentina</option>
                    <option value="CO">Colombia</option>
                    <option value="CR">Costa Rica</option>
                    <option value="ES">España</option>
                    <option value="PE">Peru</option>
                </select>
            </div>

            <div className="input-field col s12">
                <input type="submit" className="waves-effect waves-light btn-small
                btn-block teal lighten-4" value="Buscar" />
            </div>

        </div>
        </form>
    )
}
