import React from 'react'

export default function Clima({resultado}) {

//extraer los valores

const {name,main} = resultado;


if (!name) return null;


//restar grados kelvin
const kelvin = 273.15;

    return (
        <div className="card-panel blue-grey lighten-1 col s12">
            <div className="black-text">
                <h2>{name}:</h2>

                <p className="temperatura">{ parseInt (main.temp - kelvin,10) }ºC
                </p>
                <p>{ parseInt (main.temp_max - kelvin,10) } &#x2103; / { parseInt (main.temp_min - kelvin,10) } &#x2103;
                </p>


            </div>

        </div>
    )
}
